  computed: {},
  created() {
    this.$store.commit("UPDATE_CURRENT_PAGENAME", {
      subpageName: "Checklist"
    });
    this.isExist = false;
  },
  mounted() {},
  watch: {
    //triggered whenever the route path changes
    $route() {
      this.CLEAR_CURRENT_VIEW();
      this.id_inspection_record = "";
      this.current_view = {};
    }
  },
  methods: {
    VIEW_ITEM(item) {
      this.current_view = {};
      this.current_view = item;
      console.log("VIEW_ITEM: ");
      console.log(item);
      this.CLEAR_CURRENT_VIEW();
      this.id_inspection_record = item.id_inspection_record;
      this.CHECK_EXIST_RESULT(item.id_inspection_record);
    },

    CREATE_CHECKLIST() {
      console.log(
        "==> CREATE NEW CHECKLIST SHEET: id_insp: " + this.id_inspection_record
      );
      this.isLoading = true;
      axios({
        method: "post",
        url:
          "pressure-vessel-chk-list/add-all-pressure-vessel-chk-list?id_insp_record=" +
          this.id_inspection_record,
        headers: {
          Authorization: "Bearer " + JSON.parse(localStorage.getItem("token"))
        },
        data: {
          id_insp_record: this.id_inspection_record
        }
      })
        .then(res => {
          if (res.status == 201) {
            console.log(res.data);
            console.log("NEW CHECKLIST SHEET CREATED ()");
            this.isExist = true;
            this.FETCH_CHECKLIST(this.id_inspection_record);
          }
        })
        .catch(error => {
          console.log(error);
        })
        .finally(() => {
          this.isLoading = false;
        });
    },
    DELETE_CHECKLIST() {
      this.$ons.notification
        .confirm(
          "Confirm delete checklist sheet? <br/> This operation cannot be undone"
        )
        .then(res => {
          if (res == 1) {
            console.log("DELETE CHECKLIST SHEET: " + this.id_inspection_record);
            this.isLoading = true;
            axios({
              method: "delete",
              url:
                "pressure-vessel-chk-list/delete-pressure-vessel-chk-list?id_insp_record=" +
                this.id_inspection_record,
              headers: {
                Authorization:
                  "Bearer " + JSON.parse(localStorage.getItem("token"))
              },
              data: {}
            })
              .then(res => {
                if (res.status == 204) {
                  console.log("CHECKLIST SHEET DELETED");
                  this.CLEAR_CURRENT_VIEW();
                }
              })
              .catch(error => {
                console.log(error);
              })
              .finally(() => {
                this.isLoading = false;
              });
          }
        });
    },

    //Checklist
    CHECK_EXIST_RESULT(id_inspection_record) {
      console.log("CHECK RESULT EXIST (): " + id_inspection_record);
      this.isLoading = true;
      axios({
        method: "get",
        url: "pressure-vessel-chk-list/isExist?insp_id=" + id_inspection_record,
        headers: {
          Authorization: "Bearer " + JSON.parse(localStorage.getItem("token"))
        },
        data: {}
      })
        .then(res => {
          console.log(res);
          if (res.data == true) {
            this.isExist = true;
            this.FETCH_CHECKLIST(id_inspection_record);
          } else {
            this.isExist = false;
            console.log("CHECK RESULT EXIST (): FALSE");
          }
        })
        .catch(error => {
          console.log(error);
        })
        .finally(() => {
          this.isLoading = false;
        });
    },
    FETCH_CHECKLIST(id_inspection_record) {
      console.log("FECTH INSPECTION RECORD: " + id_inspection_record);
      this.isLoading = true;
      axios({
        method: "get",
        url:
          "pressure-vessel-chk-list/get-pressure-vessel-chk-list-insp-id?id_insp_record=" +
          this.id_inspection_record,
        headers: {
          Authorization: "Bearer " + JSON.parse(localStorage.getItem("token"))
        },
        data: {}
      })
        .then(res => {
          console.log("FETCH_CHECKLIST: id = " + id_inspection_record);
          if (res.status == 200 && res.data) {
            this.checklistInfo = res.data;
            //console.warn(this.checklistInfo);
          }
        })
        .catch(error => {
          console.log(error);
        })
        .finally(() => {
          this.isLoading = false;
        });
    },

    CLEAR_CURRENT_VIEW() {
      this.checklistInfo = [];
      this.isExist = false;
    },
    SCROLL_TOP(container_id) {
      document
        .getElementById(container_id)
        .scroll({ top: 0, behavior: "smooth" });
    },
    SHOW_HIDE_PANEL() {
      this.pagePanelHiding = !this.pagePanelHiding;
    },
    DATE_FORMAT(d) {
      return moment(d).format("LL");
    }
  }
};
</script>

