<script>
//API
import DxSelectBox from "devextreme-vue/select-box";
import axios from "/axios.js";
import moment from "moment";

export default {
  name: "checklist-generic",
  props: {
    checklistInfo: Array,
    record: Object,
    remark: Object
  },
  components: {
    DxSelectBox
  },
  data() {
    return {
      formData: {
        id: null
      },
      part: [],
      formDataRemark: {},
      checklistHeader: [
        {
          id: 1,
          header_content: "EXTERNAL VISUAL INSPECTION",
          topic: []
        },
        {
          id: 2,
          header_content: "INTERNAL VISUAL INSPECTION",
          topic: []
        }
      ],
      mdLvl: [
        {
          id_status: 1,
          severity_level: "Severe",
          integrity_status: "Red",
          color_code: null,
          sort_no: 1
        },
        {
          id_status: 2,
          severity_level: "Moderate",
          integrity_status: "Orange",
          color_code: null,
          sort_no: 2
        },
        {
          id_status: 3,
          severity_level: "Satisfactory",
          integrity_status: "Yellow",
          color_code: null,
          sort_no: 3
        },
        {
          id_status: 4,
          severity_level: "Good",
          integrity_status: "Green",
          color_code: null,
          sort_no: 4
        }
      ]
    };
  },
  mounted() {
    const filteredItems = this.checklistInfo.filter(
      item => item.visual_type == "External"
    );
    const filteredItems2 = this.checklistInfo.filter(
      item => item.visual_type == "Internal"
    );
    const header1 = this.checklistHeader.find(header => header.id === 1);
    const header2 = this.checklistHeader.find(header => header.id === 2);
    header1.topic = filteredItems;
    header2.topic = filteredItems2;

    this.$nextTick(() => {
      const elements = this.$el.querySelectorAll(".dx-texteditor-container");
      elements.forEach(el => {
        el.style.background = "unset";
      });
    });
  },
  created() {
    //console.warn(this.checklistInfo);
    //console.warn(this.checklistHeader);
  },
  computed: {
    localRemark: {
      get() {
        return this.remark
          ? {
              id: this.remark.id,
              id_inspection_record: this.remark.id_inspection_record,
              finding: this.remark.finding || "",
              recommendation: this.remark.recommendation || ""
            }
          : {
              finding: "no record found",
              recommendation: "no record found"
            };
      },
      set(value) {
        this.$emit("update-remark", {
          ...this.remark,
          id: this.remark.id,
          id_inspection_record: this.remark.id_inspection_record,
          finding: value.finding,
          recommendation: value.recommendation
        });
      }
    }
  },
  methods: {
    UPDATE_RESULT(item) {
      this.formData.id = item.id;
      this.formData.note = item.note;
      this.formData.id_status = item.id_status;
      axios({
        method: "put",
        url:
          "pressure-vessel-chk-list/edit-pressure-vessel-chk-list?id=" +
          item.id,
        headers: {
          Authorization: "Bearer " + JSON.parse(localStorage.getItem("token"))
        },
        data: this.formData
      })
        .then(res => {
          if (res.status == 200 && res.data) {
            console.log("==> RESULT UPDATED");
          }
        })
        .catch(error => {
          console.log(error);
          this.$ons.notification.alert(
            "Update Failed!<br/>Please try again later"
          );
        })
        .finally(() => {});
    },

    UPDATE_REMARK(item) {
      this.formDataRemark.id = item.id;
      this.formDataRemark.id_inspection_record = this.id_inspection_record;
      this.formDataRemark.finding = item.finding;
      this.formDataRemark.recommendation = item.recommendation;
      axios({
        method: "put",
        url: "piping-chk-list/edit-piping-chk-list-remark?id=" + item.id,
        headers: {
          Authorization: "Bearer " + JSON.parse(localStorage.getItem("token"))
        },
        data: this.formDataRemark
      })
        .then(res => {
          if (res.status == 200 && res.data) {
            console.log("==> REMARK UPDATED");
          }
        })
        .catch(error => {
          console.log(error);
          this.$ons.notification.alert(
            "Update Failed!<br/>Please try again later"
          );
        })
        .finally(() => {});
    },
    updateRemark(event) {
      this.$emit("update-remark", this.localRemark);
      event.preventDefault();
    },
    DATE_FORMAT(d) {
      return moment(d).format("LL");
    }
  }
};
</script>

